import useSystemStore from "@/store/system"
import { sendInfoMessage, } from "./broadcast"
const infoStore = useInfoStore()
const systemStore = useSystemStore()

// resultnullcode = 200

// 
async function register(userName, password,checkPassword) {
  const params = {
    username: userName,
    password: password,
    checkPassword:checkPassword,
  }
  const config = {
    url: '/user/register',
    params: params,
    method: 'POST'
  }
  const result = await request(config)
  // console.log('register->', result)
  if (result) {
    commitMessage('success', '')
  }
}


// 
async function login(userName, password) {
  const params = {
    username: userName,
    password: password
  }
  const config = {
    url:'/user/login',
    params: params,
    method: 'POST'
  }
  const result = await request(config)
  // console.log('login->', result)
  if (result) {
    if(!infoStore.token) commitMessage('success', '')
    infoStore.setToken(result.data)
    localStorage.setItem('token', result.data)
    localStorage.setItem('password',password)
    getUserInfo()
    return true
  }
  return false
}

// 
async function getUserInfo() {
  const token = infoStore.token ? infoStore.token : localStorage.getItem('token')
  if (token === null) {
    return;
  }
  const config = {
    url: '/user/current',
    method: 'GET',
    token: token,
  }
  const result = await request(config)
  // console.log('getUserInfo->', result)
  if (result) {
    const data = result.data
    infoStore.setToken(token)
    infoStore.setUserName(data.username)
    infoStore.setAvatarUrl(data.avatarUrl)
    infoStore.setNickName(data.nickname)
    infoStore.setId(data.id)
    infoStore.setRole(data.role)
    infoStore.setStatus(data.status)
    sendInfoMessage()
  }
}

// 
async function uploadFile(file) {
  const config = {
    url: '/file/upload',
    token: infoStore.token,
    method: 'post',
    data: {
      file:file
    }
  }
  const result = await request(config)
  // console.log('uploadFile->', result)
  if (result) {
    return result.data
  }
}


// 
async function updateUserInfo(nickName , avatarUrl) {
  const params = {
    id: infoStore.id,
    nickname: nickName,
    avatarUrl : avatarUrl
  }
  const config = {
    url: '/user/update',
    method: 'PUT',
    token: infoStore.token,
    params:params
  }
  const result = await request(config)
  if (result) {
    await login(infoStore.userName, localStorage.getItem('password'))
    getUserInfo()
    // infoStore.nickName = nickName
    // infoStore.avatarUrl = avatarUrl

  }
}

// 
async function getBlockList() {
  const config = {
    url: '/block/list',
    method: 'GET',
    token:infoStore.token
  }
  const result = await request(config)
  if (result) {
    const data = result.data
    // console.log(data)
    data.forEach((item) => {
      if (menu[item.name]) {
        menu[item.name].blockId = item.id
      }
      // 
      else {
        menu[item.name] = {
          title: item.name,
          blockId: item.id,
          icon: 'iconfont icon-new',
          scrollHeight: 1893
        }
      }
    })
    return 
  }
}

